<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教師專區</h3>
        </div>
        <Header />
        <div class="pageBoxOuter">
          <div class="stepTitle">
            <h4><span>Step3 - </span>開課資訊確認</h4>
          </div>
          <div class="pageBox" v-if="classRoom">
            <div class="processBar x3"></div>
            <div class="confirmTable tableStyle1">
              <table>
                <tr>
                  <th>班級名稱</th>
                  <td>{{ classRoom.ClassRoomName }}</td>
                </tr>
                <tr>
                  <th>課程主題</th>
                  <td>{{ classRoom.LessonTitle }}</td>
                </tr>
                <tr>
                  <th>課程內容</th>
                  <td>
                    <draggable v-model="Lessons" class="adjustable">
                      <div
                        class="adjustItem"
                        v-for="l in Lessons"
                        :key="l.LessonID"
                      >
                        <span class="type">課程</span
                        ><span class="name">{{ l.LessonName }}</span
                        ><span><a class="preview" href="page.html">預覽</a
                          ></span><span class="grabImg"><img src="/img/move.svg" /></span><span class="time">{{ l.VideoLength | mmss }}</span>
                       
                      </div>
                    </draggable>
                  </td>
                </tr>
                <tr>
                  <th>作業指派</th>
                  <td>
                    <label
                      >是
                      <input
                        type="radio"
                        name="work"
                        :value="true"
                        v-model="classRoom.HasHomeWork"
                      />
                    </label>
                    <label
                      >否
                      <input
                        type="radio"
                        name="work"
                        :value="false"
                        v-model="classRoom.HasHomeWork"
                      />
                    </label>
                  </td>
                </tr>
                <tr>
                  <th>課程代碼</th>
                  <td>
                    {{ classRoom.Code
                    }}<a
                      class="copyBtn"
                      @click="copy"
                      href="javascript:void(0);"
                      ><img src="/img/copy.svg"
                    /></a>
                  </td>
                </tr>
              </table>
            </div>
            <div class="stepControl">
              <div class="prevBtn">
                <a href="javascript:history.go(-1);">上一步</a>
              </div>
              <div class="nextBtn" v-if="classRoom.HasHomeWork">
                <a @click="next" href="javascript:void(0);">下一步</a>
              </div>
              <div class="confirmBtn" v-else>
                <a href="javascript:void(0);" @click="tempSave">暫存班級</a
                ><a href="javascript:void(0);" @click="create">建立班級</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Teacher/Header.vue";
import ClassRoom from "@/plugins/classroom.js";
import draggable from "vuedraggable";

export default {
  data() {
    return {
      classRoom: null,
      Lessons: [],
    };
  },
  components: {
    Header,
    draggable,
  },
  async mounted() {
    this.setBase("tSpace page");
    this.classRoom = ClassRoom.getClassRoom();
    var lessons = await this.$api.getLessons(this.classRoom.Lessons);
    var map = {};
    this.classRoom.Lessons.forEach((l) => (map[l] = true));
    lessons.forEach((l) => {
      if (map[l.LessonID]) {
        this.Lessons.push(l);
      }
    });
  },
  methods: {
    next() {
      var lessons = this.Lessons.map((x) => x.LessonID);
      this.classRoom.Lessons = lessons;
      ClassRoom.save();
      this.$router.push("/teacher/class/create/homework");
    },
    async tempSave() {
      var lessons = this.Lessons.map((x) => x.LessonID);
      this.classRoom.Lessons = lessons;
      this.classRoom.Status = 0;
      ClassRoom.save();
      if (ClassRoom.isNewClassRoom()) {
        await this.$api.createClassRoom(this.classRoom);
      } else {
        await this.$api.updateClassRoom(this.classRoom);
      }
      this.$router.push("/teacher/class/my");
    },
    async create() {
      var lessons = this.Lessons.map((x) => x.LessonID);
      this.classRoom.Lessons = lessons;
      this.classRoom.Status = 1;
      ClassRoom.save();
      if (ClassRoom.isNewClassRoom()) {
        await this.$api.createClassRoom(this.classRoom);
      } else {
        await this.$api.updateClassRoom(this.classRoom);
      }
      this.$router.push("/teacher/class/my");
    },
    copy() {
      window.utils.copy(this.classRoom.Code);
      setTimeout(() => {
       alert("課程代碼已複製"); 
      });
    },
  },
};
</script>